@import 'fontes';
@import 'cores';
body {
    margin: 0;
}

#banner {
    background-color: $cor-bg;
    color: $cor-texto;
    padding: 30px 0px;
    font-family: $fonte-decorativa;
    text-align: center;
    margin-bottom: 10px;
    h1 {
        font-size: 300%;
        text-shadow: 4px 4px 2px #333333;
    }
}

#formularioPessoa {
    text-align: center;
    margin-top: 30px;
    * {
        font-family: $fonte-padrao;
    }
    label {
        font-size: 120%;
        margin-right: 5px;
    }
    input[type=number] {
        height: 25px;
        font-size: 120%;
        border: 1px solid #aaaaaa;
        border-radius: 5px;
        margin-right: 20px;
    }
    input[type=submit] {
        background-color: $cor-bg;
        color: $cor-texto;
        font-family: $fonte-decorativa;
        font-size: 120%;
        padding: 10px 15px;
        border: none;
        border-radius: 5px;
    }
}

#resultado {
    display: block;
    font-family: $fonte-padrao;
    font-size: 150%;
    text-align: center;
    margin-top: 50px;
}